.codebox-container {
    display: block;
    text-align: center;
}

.codebox-container .codebox-field-wrap {
    display: inline-block;
    /*margin: 0.4rem;*/
    width: 1.8125rem;
    height: 2.8125rem;
}

/* iphone SE */
@media (max-width: 320px) {
    .codebox-container .codebox-field-wrap {
        /*margin: 0.4rem;*/
        width: 2rem;
        height: 2rem;
    }
}

.codebox-container .codebox-field-wrap input {
    width: inherit;
    height: inherit;
    line-height: inherit;
    font-size: 1.2rem;
    text-align: center;
    vertical-align: baseline;
    border: none;
    outline: none;
    color: #000000;
    background-color: transparent;
    /*border: 1px solid #d6d6d6;*/
    /*border-radius: 4px;*/
}


